.container_navbar {
	width: 15%;
	height: 100%;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	background-color:#00BCD4;
} 
.navbar{
	width: 14%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	background-color: #F6F6F6;
	position: fixed;
	top: 0;
	left: 0;
}
@media(max-width: 640px){
	.navbar{
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: transparent;
	}
}
aside{
	width: 100%;
	height: 100%;
	border-bottom: 3px solid #e4e4e4;
}
.container_navbar-links{
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	flex-direction: column;
	font-size: 15px;
}
@media(max-width: 768px){
  .container_navbar-links{
		min-width: 100%;
    font-size: 13px;
  }
}
.container_navbar-items{
	width: 100%;
	height: 70px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-image: linear-gradient(#00717F, #00BCD4);
}
.container_navbar-items-imgs{
	display: none;
}
@media(max-width: 640px){
	.container_navbar-items-imgs{
		width: 90%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
.logolowitIMG{
  width: 65%;
  height: 75%;
}
.logoLowItMobile{
	display: none;
}
.btn_menuMobile{
	display: none;
}
.logoMenuHamburguer{
	display: none;
}
@media(max-width: 640px){
	.logolowitIMG{
		display: none;
	}
	.logoLowItMobile{
		/* width: 11%;
		height: 55% !important; */
		width: 40px;
		height: 40px !important;
		display: flex;
	}
	.btn_menuMobile{
		display: flex;
		width: 11%;
		height: 11%;
		text-decoration: none;
    background-color: transparent;
    border-color: transparent;
	}
	.logoMenuHamburguer{
    width: 100%;
    height: 35%;
    display: flex;
	}
}
@media(max-width: 768px){
  .logolowitIMG{
    width: 85%;
    height: 50%;
  }
}
.nav_link{
	width: 100%;
	height: 20%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	color:#000;
}
@media(max-width: 640px){
	.nav_link{
    /* height: 34%; */
    height: 34vh;	
  }
}
@media(max-width: 768px){
  .nav_link{
    min-width: 90%;
  }
}
.nav_link:hover{
	color:#00717F;
	border-left: 4px solid #00717F;
	background-color: #CCF2F7;
}
@media(max-width: 640px){
	.nav_link:hover{
		width: 100%;
		height: 25%;
		border-left: 6px solid #00717F;
	}
}
.nav_link-label1{
	padding: 0 10px 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content:	flex-start;
	align-items: center;
	font-size: 13px;
}
.nav_link-label1:hover{
	background-color: #CCF2F7;
}
.nav_link-label1:before{
	margin-right: 10px;
	content:url('../Assets/dashpqn.png');
}
.nav_link-label1:hover::before{
	content:url('../Assets/dashpqnhover.png');
}
.nav_link-label2{
	padding: 0 10px 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 13px;
}
.nav_link-label2:hover{
	background-color: #CCF2F7;
}
.nav_link-label2:before{
	margin-right: 10px;
	content:url('../Assets/dashpqn2.png');
}
.nav_link-label2:hover::before{
	content:url('../Assets/dadoshoverpqn.png');
}
@media(max-width: 640px){
	.nav_link-label1{
		display: flex;
		justify-content: center;
		font-size: 13px !important;
		background-color: #fff;
	}
	.nav_link-label2{
		display: flex;
		font-size: 13px !important;
		justify-content: center;
		background-color: #fff;
	}
}
@media(max-width: 768px){
  .nav_link-label1{
    padding: 0 0 0;
		min-width: 100%;
		font-size: 11px;
  } 
  .nav_link-label2{
    padding: 0 0 0;
		min-width: 100%;
		font-size: 11px;
  }
}
.nav_link-img{
	width: 23%;
	height: 30%;
}
.navbar_links-list{
	display: flex;
	width: 100%;
	height: 50%;
	justify-content: space-evenly;
	flex-direction: column;
	align-items: center;
	font-family: 'Roboto', sans-serif;
}
@media(max-width: 640px){
	.navbar_links-list{
		height: 100vh;
		display: flex;
		justify-content: center;
	}
}
@media(max-width: 768px){
  .navbar_links-list{
    font-size: 13px;
  }
}
.renderLink-menu{
	width: 100%;
	height: 90%;
	display: block;
}
@media(max-width: 640px){
	.renderLink-menu{
		display: none;
	}
}

.teste{
	display: flex;
}